import { Pegawai_lisensi } from 'src/app/sdk/hr/models/pegawai-_lisensi';
import { Component, OnInit } from '@angular/core';
import { DirektoriLisensiTipeService, DirektoriPegawaiLisensiService } from 'src/app/sdk/hr/services';
import { Lisensi_tipe } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private direktoriLisensiTipeService: DirektoriLisensiTipeService,
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataPegawailisensi: Pegawai_lisensi = {}
    listLisesni: Lisensi_tipe[] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
            this.getPreeData()
            this.form()
        })

    }

    getPreeData() {
        this.direktoriLisensiTipeService.getDirektoriLisensiTipes().subscribe(
            data => {
                this.listLisesni = data.data
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai_lisensi>
    form() {
        this.dataForm = this.fb.group<Pegawai_lisensi>({
            id_lisensi_tipe: new FormControl('', [Validators.required]),
            status_lisensi: new FormControl(''),
            nomor_lisensi: new FormControl(''),
            tanggal_berlaku: new FormControl(''),
            tanggal_berakhir: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai)
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPegawailisensi = this.dataForm.value
        this.direktoriPegawaiLisensiService
            .postDirektoriPegawaiLisensis(this.dataPegawailisensi)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/profile/lainnya/lisensi/list'])
    }

}
