import { V_pegawai_lisensi } from 'src/app/sdk/hr/models/v_pegawai-_lisensi';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiLisensiService, DirektoriSpTipeService, DirektoriLisensiTipeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPegawaiLisensi: V_pegawai_lisensi[] = []
    id_training: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getLisensi()
    }


    getLisensi() {
        this.direktoriPegawaiLisensiService.getDirektoriPegawaiLisensis(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPegawaiLisensi = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiLisensiService
            .deleteDirektoriPegawaiLisensisId(id)
            .subscribe(data => {
                this.getLisensi()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }
}
